  background-color: #ecf0f5;
}
.readme-preview-markdown {
  width: 100%;
  height: 65vh;
  resize: none;
  background-color: #efefef;
}
.readme-preview-output {
  height: 65vh;
  border: 1px solid #A9A9A9;
}
.readme-preview-header {
  font-size: 22px;
  padding-bottom: 5px;
}
.btn-default:hover {
