  margin-bottom: 20px;
  font-weight: bold;
  text-align: center;
  font-size: 12pt;
  background: #3c8dbcb0;
  display: none;
}
.config-no-hide {
  padding: 15px;
  margin-bottom: 20px;
  font-weight: bold;
  text-align: center;
  font-size: 12pt;
  background: #3c8dbcb0;
}
.side-tab {
  padding: 0;
  margin-bottom: 120px;
  margin-right: 10px;
  width: 20%;
  min-width: 100px;
  position: relative;
  min-height: 300px;
  font-weight: bold;
}
.side-tab-item {
  padding: 7px;
}
.category-list {
  padding-left: 0;
}
.category-btn {
  padding: 6px 0px;
  display: inline-block;
  /* margin-bottom: 0; */
  font-weight: normal;
  width: 100%;
  cursor: pointer;
  border-radius: 15px;
  margin-bottom: 7px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25), 0 1px 3px 0 rgba(0, 0, 0, 0.2);
  text-align: center;
}
.category-btn:hover {
  background-color: #ecf0f5;
}
.category-btn-active {
  background-color: #ecf0f5;
  box-shadow: inset 0 1px 3px 0 rgba(0, 0, 0, 0.25), inset 0 1px 3px 0 rgba(0, 0, 0, 0.2);
}
.save-delete-wrapper {
  position: absolute;
  bottom: -100px;
  margin: 10px 0px;
  left: 5%;
  right: 5%;
}
.save-delete-hr {
  border: 1px dashed #222d32;
  margin-bottom: 5px;
}
.toggle-fullscreen-btn {
  text-shadow: none;
  border: none;
  margin-top: 2px;
  background-color: #ecf0f5;
}
.toggle-fullscreen-btn:hover {
  background-color: #cfd5db;
}
.save-delete-save-btn {
  text-shadow: none;
  border: none;
  margin-top: 2px;
  background-color: #ecf0f5;
}
.save-delete-save-btn:hover {
  background-color: #d9f1d9;
}
.save-delete-delete-btn {
  border: none;
  margin: 2px 0px;
  cursor: pointer;
  background-color: #ecf0f5;
}
.save-delete-delete-btn:hover {
  background-color: #ffd6d6;
}
.full-width {
  width: 100%;
}
.two-col-wrapper {
  position: relative;
}
.two-col-left {
  display: inline-block;
  width: 45%;
  padding-right: 10px;
}
.two-col-right {
  display: inline-block;
  width: 45%;
  right: 0;
  bottom: 0;
  position: absolute;
}
.col-6-left,
.col-6-right {
  vertical-align: top;
}
.preview-outer-wrapper {
  text-align: right;
  padding-top: 40px;
}
/* scrollbar */
/* width */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #c0c0c0;
  background: #fff;
  border-radius: 10px;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: #c0c0c0;
  border-radius: 10px;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #a7a7a7;
}
.maxTabsDropdown {
  max-height: 60vh;
  overflow: auto;
  z-index: 3000;
}
.miro-tabset-group-1 {
  color: #458fb8 !important;
}
.miro-tabset-group-1:hover {
  color: #377293 !important;
}
.data-filter-wrapper {
  text-align: left;
}
.no-padding-left {
  padding-left: 0px;
}
.miro-pivot-filter .form-group {
  margin-bottom: 0;
}
.miro-pivot-filter .form-group label {
  margin-bottom: 0;
}
.filter-index-list::after,
.aggregation-index-list::after {
  background-color: #fff;
}
.dropdown-menu .miro-pivot-view-button {
  width: 20px;
  padding-left: 10px;
}
.dropdown-menu .miro-pivot-view-button:hover {
  color: #F39619;
  background-color: unset;
}
.drop-index-header {
  font-size: 14px;
  font-weight: 700;
}
.drop-index-list {
  position: relative;
  width: 100%;
  min-height: 50px;
  padding: 5px;
  list-style-type: none;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 4px 0px inset;
  background-color: #f1f1f18f;
}
.pivot-hdr-col {
  border-right: 1px solid black;
}
.drop-index-item {
  min-width: 48px;
  line-height: 30px;
  text-align: center;
  border: 1px solid #e0e0e0;
  border-radius: 20px;
  padding: 5px;
  margin: 3px;
  cursor: pointer;
  display: block;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 4px;
}
.drop-index-item-selected {
  color: #000;
  background-color: #e7e7e7;
  border-color: #adadad;
  transition: all 0.5s ease;
}
.drop-index-item:hover {
  color: #000;
  background-color: #e7e7e7;
  border-color: #adadad;
  transition: all 0.5s ease;
}
.data-section {
  margin: 25pt 0 0;
  padding-top: 10pt;
  border-top: 1px solid #e7e7e7;
}
.data-section .data-section-block {
  background-color: #f1f1f18f;
  margin-right: 5px;
  padding-right: 0;
}
.data-section .data-section-block.last-section {
  margin-right: 0;
}
.data-section-header {
  font-weight: 600;
}
.toggle-custom {
  position: absolute;
  left: 6px;
}
.drop-index-list-presentation {
  padding: 0px;
}
.drop-index-item-presentation {
  line-height: 12px;
  font-size: 12px;
  text-align: center;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  padding: 5px;
  display: block;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 1px 4px;
}
.uel-item-presentation {
  display: block;
  padding: 5px 0px;
  line-height: 16px;
  margin: 0 3px 3px 0;
  padding: 1px 3px;
  background-color: #d8e0ec;
  border: 0 solid transparent;
  border-radius: 5px;
  display: inline-block;
  vertical-align: baseline;
  font-family: inherit;
  font-size: 12px;
  -webkit-font-smoothing: inherit;
}
.row-presentation {
  margin: 3px;
}
.col-md-4.column-presentation {
  min-width: 50pt;
  padding: 0px 15px 0px 0px;
}
.col-md-8.column-presentation {
  padding: 0px;
}
@media (max-width: 991px) {
  .column-presentation {
    margin-bottom: 3pt;
  }
  .col-md-4.column-presentation {
    width: 100%;
  }
  .col-md-8.column-presentation {
    padding: 0px 15px 0px 0px;
    width: 100%;
  }
}
@media (max-width: 991px) {
  .col-md-8.column-presentation {
    display: none;
  }
}
.list-presentation {
  list-style-type: none;
}
.pivot-chart-height {
  min-height: 400px;
}
.row-index-header,
.col-index-header {
  display: none;
}
.row-agg-filter {
  margin: 0;
  padding-top: 5pt;
}
.miro-pivot-height {
  height: 400px;
  height: 50vh;
  position: relative;
  min-height: 200px;
}
@media (max-width: 991px) {
  .presentation-on > .row-agg-filter > .col-md-10.presentation-show {
    padding-left: 0;
    padding-top: 5px;
  }
  .pivot-chart-height {
    min-height: 250px;
    overflow: auto;
    padding-left: 0;
    padding-right: 0;
  }
  .pivot-chart-height > div[id*="pivotChart-container"] {
    min-width: 400px;
  }
  .data-section > .row {
    flex-direction: column;
  }
  .data-section .data-section-block {
    margin-bottom: 5px;
    margin-right: 0px;
  }
  .row-agg-filter:not(.presentation-on > .row-agg-filter) {
    display: flex;
    flex-flow: column;
    padding-top: 0;
  }
  .row-agg-filter:not(.presentation-on > .row-agg-filter) > .filter-index-wrapper {
    order: 1;
    padding: 0 3pt 0 0!important;
  }
  .row-agg-filter:not(.presentation-on > .row-agg-filter) > .filter-dropdowns-wrapper {
    order: 2;
    padding: 0 3pt 5pt 0!important;
  }
  .row-agg-filter:not(.presentation-on > .row-agg-filter) > .aggregate-index-wrapper {
    order: 3;
    padding: 5pt 3pt 0 0!important;
    border-top: 1px solid;
    display: flex;
    flex-flow: column;
  }
  .row-agg-filter:not(.presentation-on > .row-agg-filter) > .aggregate-index-wrapper > .drop-index-header {
    order: 1;
  }
  .row-agg-filter:not(.presentation-on > .row-agg-filter) > .aggregate-index-wrapper > .aggregation-function {
    order: 2;
  }
  .row-agg-filter:not(.presentation-on > .row-agg-filter) > .aggregate-index-wrapper > .aggregation-function > .form-group {
    margin-bottom: 5px;
  }
  .row-agg-filter:not(.presentation-on > .row-agg-filter) > .aggregate-index-wrapper > .aggregation-index-list {
    order: 3;
  }
  .row-agg-filter:not(.presentation-on > .row-agg-filter) > .aggregate-dropdowns-wrapper {
    order: 4;
    padding: 0 3pt 5pt 0!important;
    border-bottom: 1px solid;
  }
  .row-agg-filter > .filter-index-wrapper:not(.presentation-on) {
    display: flex;
    flex-flow: column;
  }
  .row-agg-filter > .filter-index-wrapper:not(.presentation-on) > .dropdown.presentation {
    order: 1;
  }
  .row-agg-filter > .filter-index-wrapper:not(.presentation-on) > .presentation-hide {
    order: 2;
    margin-top: 5px;
  }
  .row-agg-filter > .filter-index-wrapper:not(.presentation-on) > .drop-index-header {
    order: 3;
  }
  .row-agg-filter > .filter-index-wrapper:not(.presentation-on) > .filter-index-list {
    order: 4;
    margin-top: 0;
  }
  .col-filter:not(.presentation-on > .col-filter) {
    display: flex;
    flex-flow: column;
  }
  .col-filter:not(.presentation-on > .col-filter) > .col-charttype-wrapper {
    order: 3;
    padding: 0 3pt 0 0!important;
  }
  .col-filter:not(.presentation-on > .col-filter) > .col-index-wrapper {
    order: 1;
    padding: 5pt 3pt 5pt 0!important;
  }
  .col-filter:not(.presentation-on > .col-filter) > .col-index-wrapper > .col-index-header {
    display: block;
  }
  .col-filter:not(.presentation-on > .col-filter) > .col-dropdowns-wrapper {
    order: 2;
    padding: 0 3pt 5pt 0;
    border-bottom: 1px solid;
  }
  .table-chart:not(.presentation-on > .table-chart) > .row-dropdowns-wrapper {
    padding: 5pt 3pt 5pt 0!important;
  }
  .table-chart:not(.presentation-on > .table-chart) > .row-dropdowns-wrapper > .row-index-header {
    display: block;
  }
  .miro-pivot-filter .control-label:not(.presentation-on > .miro-pivot-filter) {
    text-align: right;
  }
  .drop-index-list {
    padding: 1px;
    min-height: 40px;
    margin-bottom: 5px;
  }
  .drop-index-item {
    line-height: 28px;
    padding: 0;
    margin: 2px;
  }
  .selectize-control.plugin-remove_button [data-value] .remove {
    font-weight: 800;
    font-size: 18px;
    width: 20px;
  }
  .selectize-control.multi .selectize-input.has-items {
    padding-left: 4px;
    padding-right: 4px;
  }
  .row-agg-filter .selectize-control,
  .col-filter .selectize-control,
  .col-filter .form-group {
    margin-bottom: 2px;
  }
  .miro-pivot-height:not(.presentation-on > .miro-pivot-height) {
    height: 250px;
    height: 50vh;
  }
}
.dropdown-item-wrapper {
  display: flex;
  position: relative;
}
.dropdown-item {
  color: #333;
}
.btn-dropdown {
  text-align: left;
  transition: none;
  border: 1px solid #cccccc;
}
.btn-dropdown,
.btn-dropdown:hover,
.btn-dropdown:active,
.btn-dropdown:focus {
  background-color: white !important;
  color: #333;
}
.btn-dropdown:active,
.btn-dropdown:focus {
  border-color: #66afe9 !important;
  -webkit-box-shadow: inset 0 1px 1px rgba(102, 175, 233, 0.75), 0 0 8px rgba(102, 175, 233, 0.6);
  box-shadow: inset 0 1px 1px rgba(102, 175, 233, 0.75), 0 0 8px rgba(102, 175, 233, 0.6);
  outline: 0;
}
.btn-dropdown-menu {
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
}
.btn-dropdown-caret {
  position: absolute;
  top: 50%;
  right: 17px;
  margin-top: -3px;
  border-color: #333 transparent transparent;
  border-style: solid;
  border-width: 5px 5px 0;
}
.tableScrollCell {
  background: #f1f1f1 !important;
}
#config-main-tab-graph {
  overflow-x: hidden;
}
#config-right-graph {
  -webkit-transition: width 0.3s ease;
  -moz-transition: width 0.3s ease;
  -o-transition: width 0.3s ease;
  transition: width 0.3s ease;
}
#config-left-graph {
  -webkit-transition: margin 0.3s ease;
  -moz-transition: margin 0.3s ease;
  -o-transition: margin 0.3s ease;
  transition: margin 0.3s ease;
}
.collapse-config-left {
  display: block;
  margin-left: -50%;
}
.collapse-config-right {
  display: block;
  margin-right: -50%;
}
.btn-group-right {
  margin-bottom: 20px;
  text-align: right;
  display: block;
  justify-content: flex-end;
  display: flex;
}
.view-dropdown-wrapper {
  max-height: 400px;
  overflow: auto;
  min-width: 300px;
  max-width: 400px;
}
.view-dropdown-item {
  width: 100%;
  white-space: normal !important;
  overflow: hidden;
  text-overflow: ellipsis;
}
.miro-auto-numeric:invalid {
  border: 2px solid #F39619;
}
.label-base-scen {
  display: inline-block;
  margin-right: 10px;
}
.base-scen {
  font-weight: 400;
  font-family: Helvetica,Times,Times New Roman,serif;
  font-size: 14px;
  color: #000;
  background: #efefef;
  cursor: default;
}
.label-wrapper {
  max-height: 150px;
  resize: vertical;
  border-bottom: 1px dashed gray;
}
.label-wrapper[style*="height"] {
  max-height: unset;
}
.miro-pivot-custom-colors-wrapper {
  max-height: 300px;
  overflow-y: auto;
}
.miro-color-picker input {
  display: none;
}
.miro-color-picker span {
  display: inline-block;
  border: 0;
  padding: 0;
  margin: 0;
  width: unset;
}
.miro-color-picker label {
  margin-right: 5px;
  display: inline-block;
  text-overflow: ellipsis;
  overflow: hidden;
  vertical-align: text-top;
}
.color-picker-element {
  background: #f1f1f18f;
  margin: 1px;
  padding: 5px 0 0 0;
}
#batchLoadModal {
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.modal-dialog-fullscreen {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
.modal-content-fullscreen {
  height: auto;
  min-height: 100%;
  border-radius: 0;
}
/* mobile */
@media only screen and (max-width: 1199px) {
  .col-mobile {
    padding-left: 0;
    padding-right: 0;
  }
}
@media only screen and (max-width: 767px) {
  body > .wrapper > .content-wrapper {
    padding-top: 50px;
  }
  body > .wrapper > .main-sidebar {
    padding-top: 50px;
    position: absolute;
  }
  .nav-tabs-custom > .tab-content.tab-mobile {
    padding: 0;
  }
  .col-mobile .tabs-ul-mobile:not(.maxTabsDropdown) {
    white-space: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
    margin-bottom: 1px;
  }
  .tabs-ul-mobile .tabs-li-mobile:not(.maxTabsDropdown > .tabs-li-mobile),
  .tabs-ul-mobile .max-tabs-dropdown-label:not(.maxTabsDropdown > .tabs-li-mobile) {
    display: inline-block;
    float: none;
  }
  .col-mobile .tabs-ul-mobile::-webkit-scrollbar {
    height: 5px;
  }
  .col-mobile > .nav-tabs-custom {
    margin-bottom: 0;
  }
  .box-title-mobile > div > div > div.box-header,
  .box-title-mobile > div > div > div > div.box-header {
    padding: 7px 10px 3px 10px;
  }
  body > .wrapper > .content-wrapper > section.content {
    padding: 10px;
  }
  .tabs-ul-mobile-wrapper {
    position: relative;
  }
  .dropdown.max-tabs-dropdown-label {
    position: unset;
  }
  .dropdown.max-tabs-dropdown-label > .maxTabsDropdown {
    position: absolute;
    left: 0;
    right: 0;
  }
}
@media only screen and (max-width: 1200px) {
  #scen-split-view .nav-tabs-custom > .tab-content.tab-mobile {
    padding: 0;
  }
  #scen-split-view .col-mobile .tabs-ul-mobile:not(.maxTabsDropdown) {
    white-space: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
    margin-bottom: 1px;
  }
  #scen-split-view .tabs-ul-mobile .tabs-li-mobile:not(.maxTabsDropdown > .tabs-li-mobile),
  #scen-split-view .tabs-ul-mobile .max-tabs-dropdown-label:not(.maxTabsDropdown > .tabs-li-mobile) {
    display: inline-block;
    float: none;
  }
  #scen-split-view .col-mobile .tabs-ul-mobile::-webkit-scrollbar {
    height: 5px;
  }
  #scen-split-view .col-mobile > .nav-tabs-custom {
    margin-bottom: 0;
  }
  .box-title-mobile > #scen-split-view > div > div > div.box-header,
  .box-title-mobile > #scen-split-view > div > div > div > div.box-header {
    padding: 7px 10px 3px 10px;
  }
  #scen-split-view .box-body.box-mobile {
    padding-right: 5px;
  }
  #scen-split-view .tabs-ul-mobile-wrapper {
    position: relative;
  }
  #scen-split-view .dropdown.max-tabs-dropdown-label {
    position: unset;
  }
  #scen-split-view .dropdown.max-tabs-dropdown-label > .maxTabsDropdown {
    position: absolute;
    left: 0;
    right: 0;
  }
}
